@import './variables';
@import './libs/normalize';

@import 'components/all';

* {
    font-family: Nunito, system-ui, Tahoma, sans-serif;
}

body {
    background-color: $background;
}

.wrapper {
    width: calc(100vw - 30px);
    height: 100%;
    background-color: #fff;
    margin: 50px auto;
    border-radius: 10px;
    max-width: 1400px;

    @media screen and (max-width: 800px) {
        margin: 30px auto;
    }
}

.content {
    padding: 40px 0;

    @media screen and (max-width: 580px) {
        padding: 10px 0;
    }

    &__title {
        margin: 35px 0;
        font-size: 36px;
        font-weight: 700;
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(4, 1fr);

        @media screen and (max-width: 1400px) {
            grid-template-columns: repeat(3, 1fr);
        }

        @media screen and (max-width: 1060px) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media screen and (max-width: 800px) {
            grid-template-columns: repeat(1, 1fr);
        }
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media screen and (max-width: 1000px) {
            flex-direction: column-reverse;
        }
    }
}

.container {
    width: $container-width;
    margin: 0 auto;

    &--cart {
        max-width: 820px;
        margin: 40px auto;
        .content__title {
            margin: 0;
        }
    }
}

.cart {
    &__top {
        display: flex;
        justify-content: space-between;
        alifgn-items: center;
    }

    .content__items {
        display: block;
    }

    .content__title {
        display: flex;
        align-items: center;
        font-size: 32px;

        svg {
            position: relative;
            top: -2px;
            width: 30px;
            height: 30px;
            margin-right: 10px;
            path {
                stroke: $black;
                stroke-width: 1.9;
            }
        }
    }

    &__clear {
        display: flex;
        align-items: center;
        cursor: pointer;
        @include noselect();

        @media screen and (max-width: 580px) {
            svg {
                width: 27px;
                height: 27px;
            }
        }

        span {
            display: inline-block;
            margin-left: 7px;
            color: #b6b6b6;
            font-size: 18px;

            @media screen and (max-width: 580px) {
                display: none;
            }
        }

        span,
        svg,
        path {
            transition: all $duration ease-in-out;
        }

        &:hover {
            svg {
                path {
                    stroke: darken($color: #b6b6b6, $amount: 50);
                }
            }
            span {
                color: darken($color: #b6b6b6, $amount: 50);
            }
        }
    }

    &__item {
        display: flex;
        width: 100%;
        border-top: 1px solid $gray-line;
        padding-top: 30px;
        margin-top: 30px;

        @media screen and (max-width: 800px) {
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            row-gap: 20px;
            &-info {
                grid-column: span 4;
            }
            &-count {
                grid-column: span 3;
            }
            &-price {
                grid-column: span 3;
            }
            &-remove {
                grid-row: 1;
                grid-column-start: 6;
            }
        }

        &-img {
            display: flex;
            align-items: center;
            margin-right: 15px;
            width: 10%;

            img {
                width: 80px;
                height: 80px;
            }
        }

        &-info {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 40%;

            @media screen and (max-width: 800px) {
                width: 100%;
            }

            h3 {
                font-weight: bold;
                font-size: 22px;
                line-height: 27px;
                letter-spacing: 0.01em;
                @media screen and (max-width: 500px) {
                    font-size: 20px;
                }
            }

            p {
                font-size: 18px;
                color: #8d8d8d;
                @media screen and (max-width: 500px) {
                    font-size: 17px;
                }
            }
        }

        &-count {
            display: flex;
            align-items: center;
            width: 13%;
            flex-shrink: 0;

            @media screen and (max-width: 800px) {
                width: 50%;
            }

            &-minus {
                svg {
                    path:first-of-type {
                        display: none;
                    }
                }

                &:disabled {
                    cursor: auto;
                    border-color: #777777;

                    &:hover{
                        background-color: white;
                    }

                    &:active{
                        transform: translateY(0)
                    }

                    svg {
                        path{
                            fill: #777777;
                        }
                    }
                }
            }

            b {
                font-size: 22px;
                margin: 0 14%;
            }
        }

        &-price {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 33%;
            white-space: nowrap;

            @media screen and (max-width: 800px) {
                width: 100%;
                justify-content: right;
            }

            b {
                font-weight: bold;
                font-size: 22px;
                letter-spacing: 0.01em;
            }
        }

        &-remove {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            width: 4%;

            @media screen and (max-width: 800px) {
                justify-content: left;
            }

            .button {
                border-color: darken($color: $gray-line, $amount: 10);
            }

            svg {
                transform: rotate(45deg);

                path {
                    fill: darken($color: $gray-line, $amount: 15);
                }
            }

            .button {
                svg {
                    width: 11.5px;
                    height: 11.5px;
                    position: relative;
                }
                &:hover,
                &:active {
                    border-color: darken($color: $gray-line, $amount: 80);
                    background-color: darken($color: $gray-line, $amount: 80);
                }
            }
        }
    }

    &__bottom {
        margin: 50px 0;

        &-details {
            display: flex;
            justify-content: space-between;

            @media screen and (max-width: 580px) {
                flex-direction: column;
            }

            span {
                font-size: 22px;

                @media screen and (max-width: 580px) {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 15px;

                    span {
                        flex-shrink: 0;
                    }

                    b {
                        flex-shrink: 0;
                    }

                    .filler {
                        width: 100%;
                        border-bottom: 1px dashed darken($color: $gray-line, $amount: 20%);
                        flex-shrink: 1;
                        transform: translateY(-19px);
                        margin: 0 5px;
                    }
                }

                &:last-of-type {
                    b {
                        color: $orange;
                    }
                }
            }
        }

        &-buttons {
            display: flex;
            justify-content: space-between;
            margin-top: 40px;

            @media screen and (max-width: 580px) {
                flex-direction: column;
                margin-top: 15px;
            }

            .go-back-btn {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 210px;

                border-color: darken($color: $gray-line, $amount: 10);

                @media screen and (max-width: 580px) {
                    display: none;
                }

                span {
                    color: darken($color: $gray-line, $amount: 20);
                    font-weight: 500;
                    font-weight: 600;
                }

                &:hover {
                    background-color: darken($color: $gray-line, $amount: 90);
                    border-color: darken($color: $gray-line, $amount: 90);

                    span {
                        color: $gray-line;
                    }
                }

                svg {
                    margin-right: 12px;
                    path {
                        fill: transparent;
                        stroke-width: 2;
                    }
                }
            }

            .pay-btn {
                font-size: 16px;
                font-weight: 600;
                width: 210px;
                padding: 16px;

                @media screen and (max-width: 580px) {
                    margin: 0 auto;
                }
            }
        }
    }

    &--empty {
        margin: 0 auto;
        width: 100%;
        text-align: center;

        h2 {
            font-size: 32px;
            margin-bottom: 10px;
        }

        p {
            font-size: 18px;
            line-height: 145.4%;
            letter-spacing: 0.01em;
            color: #777777;

            br {
                @media screen and (max-width: 480px) {
                    display: none;
                }
            }
        }

        icon {
            position: relative;
            top: 2px;
        }

        img {
            display: block;
            max-width: 300px;
            width: 100%;
            margin: 45px auto 60px;
        }

        .button--black {
            padding: 12px 0 14px;

            width: 230px;
            margin: 0 auto;
            font-weight: 600;
            font-size: 18px;
        }
    }
}
